/* Minimal reset and base styles */
:root {
  --bg: #0b1220;
  --muted: #94a3b8;
  --white: #ffffff;
}

* { box-sizing: border-box; }

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: var(--bg);
  color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Inter', ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
}

/* basic typographic defaults to reduce flash of unstyled content */
h1, h2, h3, h4, h5, h6 { margin: 0; font-weight: 800; }
p { margin: 0 0 1rem 0; color: var(--muted); }

/* Make buttons and inputs look decent before Tailwind loads */
button, input, select, textarea {
  font-family: inherit;
  color: inherit;
}

/* simple layout guard for the navbar area */
nav { background: rgba(10,31,68,0.85); }

/* ensure anchors have visible color */
a { color: #7dd3fc; text-decoration: none; }
a:hover { text-decoration: underline; }

/* Bill card grid alignment */
.bill-card-grid {
  display: grid;
  grid-template-columns: 1fr;
}

@media (min-width: 640px) {
  .bill-card-grid {
    grid-template-columns: 5fr 2fr 3fr 2fr;
    gap: 1rem;
    align-items: center;
  }
}

/* Ensure consistent badge sizing */
.status-badge {
  min-width: 120px;
  text-align: center;
  display: inline-block;
}

/* Ensure consistent button sizing */
.view-button {
  min-width: 80px;
  text-align: center;
  display: inline-block;
}

/* Ensure consistent date sizing */
.date-text {
  min-width: 80px;
  text-align: center;
}
